<template lang="html">
    <x-header>
      
    </x-header>
    <div class="team_logo">
      <div class="logo">
        <img src="../../assets/team_default_logo.png" alt="" />
        <input type="file" accept="image/jpg,image/jpeg,image/png,image/gif" multiple />
      </div>
    </div>
    <div class="weui_cells weui_cells_form">
      <div class="weui_cell">
        <div class="weui_cell_hd" style="width:5%;">
          <label class="weui_label">
            <i class="fa fa-at"></i>
          </label>
        </div>
        <div class="weui_cell_bd weui_cell_primary">
          <input class="weui_input" type="text" placeholder="" v-model="team.name" />
        </div>
      </div>
      <div class="weui_cell">
        <div class="weui_cell_hd" style="width:5%;">
          <label class="weui_label">
            <i class="fa fa-tag"></i>
          </label>
        </div>
        <div class="weui_cell_bd weui_cell_primary">
          <input class="weui_input" type="text" placeholder="" v-model="team.sign" />
        </div>
      </div>
    </div>
    <div class="button_sp_area" style="margin-top:30px;padding: 10px;">
      <a @click="create" href="javascript:;" class="weui_btn weui_btn_plain_primary"> </a>
    </div>
    <toast :show.sync="loginFail" type="text"></toast>
</template>

<script>
  import XHeader from 'vux/dist/components/x-header'
  import Toast from 'vux/dist/components/toast'
  import * as teamService from '../../services/teamService'

  export default {
    data: function () {
      return {
        createFail: false,
        team: {
          name: '',
          sign: ''
        }
      }
    },
    route: {
      data (transition) {
      }
    },
    methods: {
      create () {
        if (this.name === '') {
          this.$set('createFail', true)
          return
        }
        let token = localStorage.getItem('ruzun_user_token')
        if (token === null) {
          this.$route.router.go({name: 'signin'})
          return
        }
        teamService.createTeam({team: this.team}, token)
        .then((responseJson) => {
          // 
          this.$route.router.go({name: 'team', params: {tid: responseJson.team.id}})
        })
        .catch((error) => {
          // 
          console.log(error)
          this.$set('createFail', true)
        })
      }
    },
    components: {
      XHeader,
      Toast
    }
  }
</script>

<style lang="scss">
</style>



